@import url(https://fonts.googleapis.com/css2?family=BioRhyme:wght@800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{background-color:black}.container{align-items:center;text-align:center;color:white;margin-top:80px;height:100vh}@media (max-width: 510px){.container{align-items:center;text-align:center}}.main-image{width:400px}@media (max-width: 510px){.main-image{width:350px}}

h2{font-family:'BioRhyme', serif}.toolbox{height:100vh}.tool-icons{display:flex;justify-content:space-around;padding:40px}h2{font-size:1.8rem}

.skills-desc{padding:20px;font-family:'Fjalla One', sans-serif;text-align:center;font-size:2rem;color:white}h2{font-size:1.8rem}

.intrest-desc{padding:20px;text-align:center;font-family:'Fjalla One', sans-serif;font-size:2rem;color:white}h2{font-size:1.8rem}

.card{width:250px;background-color:#252525;color:white}@media (max-width: 510px){.card{width:200px;margin-left:110px;margin-right:60px}}@media (max-width: 420px){.card{width:200px;margin-left:90px}}.card-title{color:white}a{color:white}

.section{margin-top:30px;height:100vh}@media (max-width: 510px){.section{flex-direction:column;margin-top:200px;align-items:center}}h2{font-size:1.8rem;color:white}.project-cards{display:flex;justify-content:space-around}@media (max-width: 510px){.project-cards{flex-direction:column}}

.footer{height:30px;position:relative;color:black;background-color:white;text-align:center}@media (max-width: 510px){.footer{margin-bottom:0}}

